@import "sass/index";

.box {
  color: white;
  padding: 40px 0;
}
.title {
  margin: -15px 0 20px 45px;
  font-size: 1.4rem;
  color: $m_secondary-main;
  text-align: left;
}
.visibleWrapper {
  width: 100%;
  max-width: 350px;
  margin: 0 auto;
  padding: 0 20px;
}
.visibleInput {
  height: 40px;
  color: black;
}
.invisibleInput {
  display: none;
}
.coinWrapper {
  position: relative;
}
.coinInput {
  padding-left: 20px;
}
.form {
  margin-top: 30px;
  padding: 0 20px;
  width: 100%;

  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-direction: column;

  & input {
    display: none;
  }
}
.button {
  background: $m_secondary-main !important;
}
.close {
  position: absolute;
  top: 0;
  right: 0;

  & path {
    stroke: white;
  }
}
